<template>
<div class="searchPanel" v-show="searchVisibile">
    <el-input ref="searchBox" type="text" id="searchBox" v-model="searchInfo" @keyup.enter="getSearchList">
    <template #suffix>
        {{searchIndex}}/{{searchTotal}}
    </template>
    </el-input>
    <span style="margin-right: 1px; color: #aaaa">|</span>
    <i class="el-icon el-icon-arrow-up" @click="searchPrev" title="上一个"></i>
    <i class="el-icon el-icon-arrow-down" @click="searchNext" title="下一个"></i>
    <i class="el-icon el-icon-close" @click="closeSearchBox"></i>
</div>
</template>
<script lang="ts">
import { useStore } from '@/store'
import { defineComponent, reactive, toRefs, watch, onMounted, ref, nextTick } from 'vue'
export default defineComponent ({
  name: 'Search',
  props: {
    listItem: {
        type: Array,
        default: () => {
            return []        }
    },
    labelList: {
        type: Object,
        default: () => {
            return {}
        }
    }
  },
  setup(props, context){
    const store = useStore()
    const searchBox = ref()
    let searchInfo = ref('')
    const dataMap = reactive({
      searchVisibile: false,
      // searchInfo: String(),
      searchIndex: 0,
      searchTotal: 0,
      searchList: Array(),
      searchDirection: 'down',
      compositionFlag: true,
      sourceListItem: Array()
    })
    watch(props.listItem, (value: any) => {
			dataMap.sourceListItem = value
      if (dataMap.searchVisibile && searchInfo.value) {
        getSearchList()
      }
		})
    watch(searchInfo, (value: any) => {
      if (!searchInfo.value) {
        searchRset()
      } else {
        if (dataMap.compositionFlag) {
          getSearchList()
        }
      }
		})
    const searchRset = () => {
      searchInfo.value = '';
      dataMap.searchTotal = 0;
      dataMap.searchIndex = 0;
      clearLabelMark();
    }
    const clearLabelMark =  () => {
        let tags = document.getElementsByClassName('result');
        if (tags.length > 0) {
          for (let i=tags.length-1;i>=0;i--) {
              let pNode:any = tags[i].parentNode,
                  text = pNode.innerText.trim()
              pNode.innerHTML = text.replace(/[\r\n]/g,"")
          }
        }
    }
    const searchNext = () => {
      if (dataMap.searchIndex >= dataMap.searchTotal) {
        return;
      }
      // const index = this.searchList[this.searchIndex++].pos;
      // this.jump(index);
      dataMap.searchDirection = 'down'
      const index = dataMap.searchList[dataMap.searchIndex++].pos;
      jump(index, dataMap.searchList[dataMap.searchIndex-1].item[0].key);
    }
    const searchPrev = () => {
      if (dataMap.searchIndex <= 0) {
        return;
      }
      dataMap.searchDirection = 'up';
      const index = dataMap.searchList[--dataMap.searchIndex].pos;
      jump(index, dataMap.searchList[dataMap.searchIndex].item[0].key);
    }
    const jump = (index:number, key: any) => {
      context.emit('update:scrollToItem', index)
      nextTick(()=>{
          var timer = setTimeout(() => {
              searchHtml(index, key)
              clearTimeout(timer);
          }, 0);
      })
    }
    const getSearchList = () => {
      dataMap.searchList = [];
      dataMap.searchDirection = 'down';
      dataMap.sourceListItem = store.state.app.data
      dataMap.sourceListItem.forEach((item: any, i) => {
        Object.keys(props.labelList).map(key => {
          // 对比item title是否匹配
          if (key.indexOf(searchInfo.value) > -1) {
            dataMap.searchList.push({
              PLRId: item.data['PLRId'],
                    pos: i,
                    item: [{
                      key: `${key}（${i+1}）`
                    }],
                    type: 'label'
                });
            }
           // 对比字段名是否有匹配
           props.labelList[key].forEach((ele: any) => {
              if (ele.indexOf(searchInfo.value) > -1) {
                  dataMap.searchList.push({
                      PLRId: item.data['PLRId'],
                      pos: i,
                      item: [{
                          key: ele
                      }],
                      type: 'label'
                  });
              }
          })
         })
        // 对比字段值是否有匹配
        Object.keys(item.data).map(key => {
          if (item.data[key] instanceof Array) {
            item.data[key].forEach((child: any) => {
              Object.keys(child).map(k => {
                const val = child[k] ? child[k].toString() : '';
                if (val.indexOf(searchInfo.value) > -1) {
                  dataMap.searchList.push({
                    PLRId: item.data['PLRId'],
                    pos: i,
                    item: [{
                      key: child[k]
                    }],
                    type: 'value'
                  });
                }
              })
            })
          } else {
            const val = item.data[key] ? item.data[key].toString() : '';
            if (val.indexOf(searchInfo.value) > -1) {
              dataMap.searchList.push({
                PLRId: item.data['PLRId'],
                pos: i,
                item: [{
                  key: item.data[key]
                }],
                type: 'value'
              });
            }
          }
        })
      });
      if (dataMap.searchList.length > 0) {
        dataMap.searchTotal = dataMap.searchList.length;
        console.log("this.searchIndex:" + dataMap.searchIndex)
        console.log("this.searchTotal:" + dataMap.searchTotal)
        if (dataMap.searchIndex >= 0 && dataMap.searchIndex < dataMap.searchTotal) {
          const index = dataMap.searchList[dataMap.searchIndex++].pos;
          jump(index, dataMap.searchList[dataMap.searchIndex-1].item[0].key);
        }
      }
      return false;
    }
    const searchHtml = (index, key) => {
      let index_direction;
      if (dataMap.searchDirection === 'up' && dataMap.searchList.length > 1 && dataMap.searchIndex < dataMap.searchList.length - 1) {
        index_direction = dataMap.searchIndex + 1
      } else if (dataMap.searchDirection === 'up' && dataMap.searchList.length <= 1 ) {
        index_direction = dataMap.searchIndex
      } else {
        index_direction = dataMap.searchIndex - 1
      }
      // lable 标记
      if (dataMap.searchList[index_direction].type && dataMap.searchList[index_direction].type === 'label') {
          clearLabelMark();
          labelMark(index, key);
          return false;
      }
      // 字段值标记
      nextTick(() => {
        // const wrap: any = document.getElementById('wrap_'+ index +''),
        const wrap: any = Array.prototype.slice.call(document.querySelectorAll('.vue-recycle-scroller__item-view'))
        var wrapChild: any 
        Array.prototype.map.call(wrap, ele => {
          if (ele.children[0].dataset['index'] == index) {
            wrapChild = ele
          }
        })
        const inpts = Array.prototype.slice.call(wrapChild.getElementsByTagName('input')),
              texts = Array.prototype.slice.call(wrapChild.getElementsByTagName('textarea')),
              nodeList = inpts.concat(texts);
        // nodeList.forEach(item => {
        //   if (item.value === key || !key) {
        //     const value = item.value,
        //           start = value.indexOf(searchInfo.value),
        //           end = start + searchInfo.value.length; 
        //     if (start > -1) {
        //       item.focus();
        //       item.setSelectionRange(start, end);
        //       return false;
        //     }
        //   }
        // })
        for(let i=0;i<nodeList.length;i++) {
          if (nodeList[i].value === key || !key) {
            const value = nodeList[i].value,
                  start = value.indexOf(searchInfo.value),
                  end = start + searchInfo.value.length; 
            if (start > -1) {
              nodeList[i].focus();
              nodeList[i].setSelectionRange(start, end);
              break;
            }
          }
        }
      })
    }
    const labelMark =  (index, key) => {
        // const wrap: any = document.getElementById('wrap_'+ index +''),
        const wrap: any = Array.prototype.slice.call(document.querySelectorAll('.vue-recycle-scroller__item-view'))
        var wrapChild: any 
        Array.prototype.map.call(wrap, ele => {
          if (ele.children[0].dataset['index'] == index) {
            wrapChild = ele
          }
        })
        const title = wrapChild.getElementsByClassName('pub-form-title') ? wrapChild.getElementsByClassName('pub-form-title')[0] : null,
            labels = wrapChild.getElementsByClassName('label'),
            regExp = new RegExp(searchInfo.value+'(?!([^<]+)?>)', 'ig');
        if (title) {
           if (title.innerHTML === key) {
                title.innerHTML = title.innerHTML.replace(regExp, "<span  class='result'>" + searchInfo.value + "</span>")
                return false;
            }
        }
        for(let i=0;i<labels.length;i++) {
            if (labels[i].innerHTML === key) {
                labels[i].innerHTML = labels[i].innerHTML.replace(regExp, "<span  class='result'>" + searchInfo.value + "</span>")
                return false;
            }
        }
    }
    const closeSearchBox =  () => {
      searchInfo.value = ''
      dataMap.searchVisibile = false
      clearLabelMark();
    }

    // directives: {
    //   focus: {
    //     // 指令的定义
    //     inserted: function(el) {
    //       el.children[0].focus();
    //     }
    //   }
    // }
    onMounted(() => {
      dataMap.sourceListItem = store.state.app.data
      const self = this;
      window.addEventListener('keydown', function(event){ 
        const e:any = event || window.event;
        if(e.metaKey && e.keyCode === 70){ 
          e.returnValue = false;
          dataMap.searchVisibile = !dataMap.searchVisibile;
          if (dataMap.searchVisibile) {
            nextTick(() => {
              searchBox.value.focus();
            })
          }
          return false;  
        }
        // 监听ctrl+f
        if (e.ctrlKey) {
            if (e.keyCode === 70) {
              e.returnValue = false;
              dataMap.searchVisibile = true;
              if (dataMap.searchVisibile) {
                nextTick(() => {
                  searchBox.value.focus();
                })
              }
              return false;
            }
        }
      })
      // 优化搜索输入拼音时不搜索，打完字再走方法
      nextTick(() => {
        const inputDom: any = document.getElementById('searchBox');
        inputDom.addEventListener('compositionstart', event => {
          dataMap.compositionFlag = false;
        })
        inputDom.addEventListener('compositionend', event => {
          dataMap.compositionFlag = true;
        })
      })
    })
    return {
      ...toRefs(dataMap),
      searchRset,
      clearLabelMark,
      searchNext,
      searchPrev,
      jump,
      getSearchList,
      searchHtml,
      labelMark,
      closeSearchBox,
      searchBox,
      searchInfo,
      store
    }
  }
  // computed: {
  //   items() {
  //     const list = dataMap.sourceListItem;
  //     // console.log('computed', list);
  //     return list;
  //   }
  // },
})
</script>
<style scoped>
.top-button {
  float: right;
}
.inputDemo {
  border: 1px solid #ccc;
}

::v-deep.normal{display:inline-block; font-style:normal; background: #afaa59;}
.searchPanel >>> .el-input__inner {
  border: none !important;
}
.searchPanel >>> .el-input {
  width: 220px !important;
  margin: 0px 15px 0px 15px;
}
.el-icon-close:hover {
  border-radius: 15px;
  background-color: #e1e1e1;
}
.el-icon {
  margin-left: 3px;
  cursor: pointer;
}
.searchPanel {
  position: fixed;
  right: 60px;
  top: 0px;
  z-index: 99;
  width: 345px;
  height: 45px;
  background-color: white;
  line-height: 45px;
  border: 1px #aaaaaa solid;
  border-radius: 5px;
  box-shadow: 1px 1px 3px 1px #aaaaaa;
}
</style>
